import React from "react";
export default function ItemList(props) {
    function deleteTable(id) {
        let listOfItems = document.querySelector("#tableBody");
        for (let i = 0; listOfItems.children.length; i++) {
            if (listOfItems.children[i].getAttribute("id") == id) {
                listOfItems.removeChild(listOfItems.children[i]);
                break;
            }
        }
    }
    const styling = props.background == "sun" ? {
        background: "none",
        color: "#0c0e16"
    } : {
        background: "#1E2139",
        border: "0",
        color: "white"
    }
    return (

        <tr id={props.id}>
            {/* element row should have a unique id */}
            <td><input type="text" className="input first" required style={styling} /></td>
            <td><input type="number" className="input second" min="0" required style={styling} /></td>
            <td><input type="number" className="input third" min="0" required style={styling} /></td>
            <td><input type="number" className="input fourth" min="0" required style={styling} /></td>
            <td><img src="./assets/icon-delete.svg" className="fifth"
                onClick={() => deleteTable(props.id)}
                style={{ cursor: "pointer" }} /></td>
        </tr>
    )
}