
import './App.css';
import HomePage from './components/HomePage';

function style(value) {
  !(value == "sun") ? document.getElementsByTagName("body")[0].style = "background-color: #f2f2f2;" :
    document.getElementsByTagName("body")[0].style = "background-color: #141625;";
  console.log(value);
}
function App() {
  return (
    <HomePage setB={value => style(value)} />
  )
}

export default App;
