import React, { useEffect, useState } from "react";
import "./NewInvoice.css";
import ItemList from "./ItemList";
export default function NewInvoice(props) {
    const [paymentTerms, setPaymentTerms] = useState("Dropdown button");
    const [listOfitems, setListOfItems] = useState([Math.random().toString(36).substr(2, 9)]);
    const [currentBackground, setCurrentBackground] = useState()
    useEffect(() => {
        if (props.backgroundColor == "moon") {
            setCurrentBackground({ backgroundColor: "#252945" });
            let list = document.querySelectorAll("#formElement input");
            let list2 = document.querySelectorAll(".formInputText");
            let list3 = document.querySelectorAll("#itemListHeading .itemName");
            let list4 = document.querySelectorAll("#calenderAndInvoice .dropdown-item");
            for (let i = 0; i < list.length; i++) {
                list[i].style = "background:#1E2139; border:0; color:white";
            }
            for (let i = 0; i < list2.length; i++) {
                list2[i].style = "color:#DFE3FA";
            }
            for (let i = 0; i < list3.length; i++) {
                list3[i].style = "color:#DFE3FA";
            }
            for (let i = 0; i < list4.length; i++) {
                list4[i].style = "color:#DFE3FA;background:#1E2139";
            }

        }
        else {
            setCurrentBackground({ backgroundColor: "rgba(0, 0, 0, 0.06)" })
            let list = document.querySelectorAll("#formElement input");
            let list2 = document.querySelectorAll(".formInputText");
            let list3 = document.querySelectorAll("#itemListHeading .itemName");
            let list4 = document.querySelectorAll("#calenderAndInvoice .dropdown-item");
            for (let i = 0; i < list.length; i++) {
                list[i].style = "background:none; color:#0c0e16";
            }
            for (let i = 0; i < list2.length; i++) {
                list2[i].style = "color:#7e88c3";
            }
            for (let i = 0; i < list3.length; i++) {
                list3[i].style = "color:#7e88c3";
            }
            for (let i = 0; i < list4.length; i++) {
                list4[i].style = "color:#0c0e16;background:none";
            }
        }
    }, [props.backgroundColor])

    return (
        <>
            <form id="formElement"
                style={currentBackground}
            >
                <div id="invoiceHeading"
                    style={props.backgroundColor == "sun" ? { color: "#0c0e16" } : { color: "white" }}
                >New Invoice</div>
                {/* part one */}
                <div>
                    <div className="partName">Bill From</div>
                    <label className="formInputText">Street Address</label>
                    <input type="text" className="input" id="senderAddress" required></input>
                    <div id="billFromAddress">
                        <div className="section1">
                            <label className="formInputText">City</label>
                            <input type="text" className="input" id="senderCity" required></input>
                        </div>
                        <div className="section1">
                            <label className="formInputText">Post Code</label>
                            <input type="text" className="input" id="senderPostcode" required></input>
                        </div>
                        <div className="section1">
                            <label className="formInputText">Country</label>
                            <input type="text" className="input" id="senderCountry" required></input>
                        </div>
                    </div>
                </div>
                {/* part two */}
                <div>
                    <div className="partName">Bill To</div>
                    <div>
                        <div className="formInputText">Client's Name</div>
                        <input type="text" className="input" id="clientName" required />
                    </div>
                    <div>
                        <div className="formInputText">Client's Email</div>
                        <input type="email" className="input" id="clientEmail" />
                    </div>
                    <div>
                        <div className="formInputText">Street address</div>
                        <input type="text" className="input" id="clientAddress" required />
                    </div>
                    <div id="billToAddress">
                        <div className="section2">
                            <label className="formInputText">City</label>
                            <input type="text" className="input" id="clientCity" required></input>
                        </div>
                        <div className="section2">
                            <label className="formInputText">Post Code</label>
                            <input type="text" className="input" id="clientPostcode" required></input>
                        </div>
                        <div className="section2">
                            <label className="formInputText">Country</label>
                            <input type="text" className="input" id="clientCountry" required></input>
                        </div>
                    </div>
                    <div id="calenderAndInvoice">
                        <div className="cmp">
                            <div className="formInputText">Invoice Date</div>
                            <input type="date" id="invoiceDate" required />
                        </div>
                        <div className="cmp">
                            <div className="formInputText">Payment Terms</div>
                            <div className="dropdown">
                                <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenu" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"
                                    style={props.backgroundColor == "sun" ? { color: "#0c0e16", background: "none", border: "2px solid #dfe3fa" } :
                                        { color: "#DFE3FA", border: "0", background: "#1E2139" }}
                                >
                                    {paymentTerms}
                                </button>
                                <div className="dropdown-menu" aria-labelledby="dropdownMenu" onClick={(event) => setPaymentTerms(event.target.textContent)}
                                    style={props.backgroundColor == "sun" ? { background: "none" } : { background: "#1E2139" }}
                                >
                                    <a className="dropdown-item" href="#">Net 1 Day</a>
                                    <a className="dropdown-item secondDropdown" href="#">Net 7 Day</a>
                                    <a className="dropdown-item thridDropdown" href="#">Net 14 Day</a>
                                    <a className="dropdown-item" href="#">Net 30 Day</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className="formInputText">Project Description</div>
                        <input type="text" className="input" id="description" />
                    </div>
                </div>
                {/* third part */}
                <div id="itemHeading">Item List</div>
                <table className="table">
                    <thead>
                        <tr id="itemListHeading">
                            <th id="item-name" className="itemName">Item Name</th>
                            <th id="qty" className="itemName">QTY.</th>
                            <th id="price" className="itemName">Price</th>
                            <th id="total" className="itemName">Total</th>
                        </tr>
                    </thead>
                    <tbody id="tableBody">
                        {listOfitems.map(item => (<ItemList key={item} id={item} background={props.backgroundColor} />))}
                    </tbody>
                </table>
                <button type="button" id="addNewItem"
                    onClick={() => {
                        setListOfItems([...listOfitems,
                        Math.random().toString(36).substr(2, 9)])
                    }}
                    style={props.backgroundColor == "sun" ? { color: "#7E88C3", background: "none" } :
                        { color: "#DFE3FA", background: "#1E2139", border: "0" }}

                >
                    + Add New Item
                </button>
            </form>
        </>
    )
}