import React from "react";
import "./ViewInvoiceStats.css";
import ViewInvoiceItems from "./ViewInvoiceItems";

export default function ViewInvoiceStats(props) {
    return (
        <div id="viewInvoiceStats"
            style={props.background == "sun" ? { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
        >
            <div id="firstStats">
                <div>
                    <span
                        style={{ color: "#888EB0" }}
                    >#</span>
                    <span id="viewInvoiceId"
                        style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                    >{props.data.id}</span>
                    <div id="description"
                        style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                    >{props.data.description}</div>
                </div>
                <div id="userAddress"
                    style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                >
                    <div id="userStreet">{props.data.senderAddress.street}</div>
                    <div>{props.data.senderAddress.city}</div>
                    <div>{props.data.senderAddress.postCode}</div>
                    <div>{props.data.senderAddress.country}</div>
                </div>
            </div>
            <div id="secondStats">
                <div>
                    <div>
                        <div className="statsHeading"
                            style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                        >Invoice Date</div>
                        <div className="statsHeadingInfo"
                            style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                        >{props.data.createdAt}</div>
                    </div>
                    <div id="paymentDue">
                        <div className="statsHeading"
                            style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                        >Payment Due</div>
                        <div className="statsHeadingInfo"
                            style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                        >{props.data.paymentDue}</div>
                    </div>
                </div>
                <div id="clientInfo">
                    <div className="statsHeading"
                        style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                    >Bill To</div>
                    <div className="statsHeadingInfo"
                        style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                    >{props.data.clientName}</div>
                    <div id="ciientAddress"
                        style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                    >
                        <div id="clientStreet">{props.data.clientAddress.street}</div>
                        <div>{props.data.clientAddress.city}</div>
                        <div>{props.data.clientAddress.postCode}</div>
                        <div>{props.data.clientAddress.country}</div>
                    </div>
                </div>
                <div>
                    <div className="statsHeading"
                        style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
                    >Sent to</div>
                    <div className="statsHeadingInfo"
                        style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                    >{props.data.clientEmail}</div>
                </div>
            </div>
            <div id="thirdStats" className="table-responsive"
                style={props.background == "sun" ? { backgroundColor: "#f9fafe" } : { backgroundColor: "#252945" }}
            >
                <table className="table table-hover">
                    <thead>
                        <tr>
                            <th className="tableHeading"
                                style={props.background == "sun" ? { color: "#7e88c3" } : { color: "#DFE3FA" }}
                            >Item Name</th>
                            <th className="tableHeading"
                                style={props.background == "sun" ? { color: "#7e88c3" } : { color: "#DFE3FA" }}
                            >QTY.</th>
                            <th className="tableHeading"
                                style={props.background == "sun" ? { color: "#7e88c3" } : { color: "#DFE3FA" }}
                            >Price</th>
                            <th className="tableHeading"
                                style={props.background == "sun" ? { color: "#7e88c3" } : { color: "#DFE3FA" }}
                            >Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.data.items && props.data.items.map(item => (
                            <ViewInvoiceItems
                                key={item.name}
                                name={item.name}
                                quantity={item.quantity}
                                price={item.price}
                                total={item.total}
                                background={props.background}
                            />
                        ))}
                    </tbody>
                </table>
            </div>
            <div id="fourthStats"
                style={props.background == "sun" ? { backgroundColor: "#373b53" } : { backgroundColor: "#0C0E16" }}
            >
                <div id="amountDue">Amount Due</div>
                <div id="amountDuePrice">${props.data.total}</div>
            </div>
        </div>
    )
}