import React, { useEffect, useState } from "react";
import "./SideBar.css";

export default function SideBar(props) {
    const [nextState, setNextState] = useState("moon");
    const [currentState, setCurrentState] = useState();
    function changeState(value) {
        setNextState(value);
        props.sunOrMoon(value);

    }
    function change() {
        props.backgroundColor == "sun" ? setCurrentState({ backgroundColor: "#494e6e" }) : setCurrentState({ backgroundColor: "#1E2139" });
    }
    useEffect(() => change(), [props.backgroundColor])
    return (
        <div id="sidebar" style={currentState}>
            <div id="logoContainer">
                <img src="./assets/logo.svg"
                    alt="image logo"
                    id="logo-image" />
            </div>
            <span>
                {nextState === "moon" ? <img src="./assets/icon-moon.svg" onClick={() => {
                    nextState === "moon" && changeState("sun")
                }} className="themeColor" /> : <img src="./assets/icon-sun.svg" onClick={() => {
                    nextState === "sun" && changeState("moon")
                }}
                    className="themeColor" />}

                <div id="myImageContainer">
                    <img
                        src="./assets/my-profile.jpg"
                        id="myImage" />
                </div>
            </span>

        </div>
    )


}