import React, { useEffect, useState } from "react";
import SideBar from './SideBar';
import MainPageHeading from "./MainPageHeading";
import "./HomePage.css";
import Invoice from "./Invoice";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import ViewInvoice from "./ViewInvoice";
import NoInvoice from "./NoInvoice";
import NewInvoice from "./NewInvoice";
import ConfirmDeletion from "./ConfirmDeletion";

export default function HomePage(props) {
    const [invoices, setInvoices] = useState(null);
    const [clickedId, setClickedId] = useState(0);
    const [sortBy, setSortBy] = useState("All");
    const [numberOfInvoices, setNumberOfInvoices] = useState(0);
    const [deleteId, setDeleteId] = useState();
    const [status, setStatus] = useState();
    const [dayOrNight, setDayOrNight] = useState("sun");
    window.onresize = () => {
        if (window.innerWidth > 992) {
            document.querySelector("#sidebar").style = "height:100%";
        } else {
            document.querySelector("#sidebar").style = "height:100vw";
        }
    }

    useEffect(() => {
        if (localStorage.getItem("isFirstTime") != null) {
            loadFromStorageToState();
            console.log("hee");

        } else {
            localStorage.setItem("isFirstTime", "false");
            fetch("./data.json")
                .then(response => response.json())
                .then(value => {
                    value.map(item => {
                        localStorage.setItem(item.id, JSON.stringify(item));
                    });
                    //setting the values in localstorage to invoices
                    let list = [];
                    for (let i = 0; i < localStorage.length; i++) {
                        if (localStorage.getItem(localStorage.key(i)) == "false") {
                            continue;
                        }
                        list = [...list, JSON.parse(localStorage.getItem(localStorage.key(i)))];
                    }
                    setInvoices(list);
                    setNumberOfInvoices(localStorage.length - 1);
                })
        }
    }, [])


    function loadFromStorageToState() {
        //setting the values in localstorage to invoices
        let list = [];
        for (let i = 0; i < localStorage.length; i++) {
            if (localStorage.getItem(localStorage.key(i)) == "false") {
                continue;
            }
            list = [...list, JSON.parse(localStorage.getItem(localStorage.key(i)))];
        }
        setInvoices(list);
        setNumberOfInvoices(localStorage.length - 1);
        console.log(list);
    }


    function invoiceNumbers(value) {
        setNumberOfInvoices(value);
        return true;
    }
    function closeSideBar() {
        document.querySelector(".sidebarContainer").style = "height:0";
        document.querySelector("#homePage").style = "background: #F2F2F2;";
        document.querySelector("#formElement").style = "display:none";
        if (window.innerWidth > 992) {
            document.querySelector("#sidebar").style = "height:850px";
        }
        if (window.innerWidth < 992 && document.querySelector("#saveOrDeleteBtn") != null) {
            document.querySelector("#saveOrDeleteBtn").style = "display:none:";

        }
        if (window.innerWidth < 992 && document.querySelector("#cancelOrSave") != null) {
            document.querySelector("#cancelOrSave").style = "display:none:";
        }
        if (window.innerWidth < 992 && document.querySelector("#viewInvoice") != null) {
            document.querySelector("#viewInvoice").style = "display:block";
            console.log("sdfsdffsdsaf");
        }
        if (dayOrNight == "sun") {
            document.querySelector("#sidebar").style = "background-color: rgb(73, 78, 110)";
        } else {
            document.querySelector("#sidebar").style = "background-color: rgb(30, 33, 57)";
        }
    }
    function changeStatus(id, newStatus) {
        let newInvioce = invoices.find(item => item.id == id);
        newInvioce.status = newStatus;
        localStorage.setItem(id, JSON.stringify(newInvioce));
        let list = [];
        for (let i = 0; i < localStorage.length; i++) {
            if (localStorage.getItem(localStorage.key(i)) == "false") {
                continue;
            }
            list = [...list, JSON.parse(localStorage.getItem(localStorage.key(i)))];
        }
        setInvoices(list);
    }
    function deleteInvoice(id) {
        localStorage.removeItem(id);
        let list = [];
        for (let i = 0; i < localStorage.length; i++) {
            if (localStorage.getItem(localStorage.key(i)) == "false") {
                continue;
            }
            list = [...list, JSON.parse(localStorage.getItem(localStorage.key(i)))];
        }
        setInvoices(list);
        setNumberOfInvoices(numberOfInvoices - 1);
    }
    function saveChanges() {

        let userId = document.querySelector("#invoiceHeading").textContent.substring(6);
        let currentInvoice = invoices.find(item => item.id == userId);

        let invoiceItems = document.querySelector("#tableBody").children;
        let listOfInvoiceItems = [];
        let runningTotal = 0;
        for (let i = 0; i < invoiceItems.length; i++) {
            let newItem = {
                name: invoiceItems[i].children[0].children[0].value,
                quantity: invoiceItems[i].children[1].children[0].value,
                price: invoiceItems[i].children[2].children[0].value,
                total: invoiceItems[i].children[3].children[0].value,
            }
            runningTotal += parseInt(newItem.total);
            listOfInvoiceItems = [...listOfInvoiceItems, newItem];
        }


        currentInvoice.id = userId;
        currentInvoice.createdAt = new Date().toISOString().substring(0, 10);
        currentInvoice.paymentDue = document.querySelector("#invoiceDate").value;
        currentInvoice.description = document.querySelector("#description").value;
        currentInvoice.paymentTerms = document.querySelector("#dropdownMenu").textContent.substring(4, 6).trim();
        currentInvoice.clientName = document.querySelector("#clientName").value;
        currentInvoice.clientEmail = document.querySelector("#clientEmail").value;
        currentInvoice.status = status;

        currentInvoice.senderAddress.street = document.querySelector("#senderAddress").value;
        currentInvoice.senderAddress.city = document.querySelector("#senderCity").value;
        currentInvoice.senderAddress.postCode = document.querySelector("#senderPostcode").value;
        currentInvoice.senderAddress.country = document.querySelector("#senderCountry").value;


        currentInvoice.clientAddress.street = document.querySelector("#clientAddress").value;
        currentInvoice.clientAddress.city = document.querySelector("#clientCity").value;
        currentInvoice.clientAddress.postCode = document.querySelector("#clientPostcode").value;
        currentInvoice.clientAddress.country = document.querySelector("#clientCountry").value;

        currentInvoice.items = [
            ...listOfInvoiceItems
        ]
        currentInvoice.total = runningTotal;
        localStorage.setItem(currentInvoice.id, JSON.stringify(currentInvoice));
        // setInvoices([...invoices.filter(item => item.id != userId), currentInvoice]);
        let list = [];
        for (let i = 0; i < localStorage.length; i++) {
            if (localStorage.getItem(localStorage.key(i)) == "false") {
                continue;
            }
            list = [...list, JSON.parse(localStorage.getItem(localStorage.key(i)))];
        }
        setInvoices(list);
        closeSideBar();
    }

    function newInvoice(statusType) {
        let invoiceItems = document.querySelector("#tableBody").children;
        let listOfInvoiceItems = [];
        let runningTotal = 0;
        for (let i = 0; i < invoiceItems.length; i++) {
            let newItem = {
                name: invoiceItems[i].children[0].children[0].value,
                quantity: invoiceItems[i].children[1].children[0].value,
                price: invoiceItems[i].children[2].children[0].value,
                total: invoiceItems[i].children[3].children[0].value,
            }
            runningTotal += parseInt(newItem.total);
            listOfInvoiceItems = [...listOfInvoiceItems, newItem];
        }

        let createNewInvoice = {
            id: Math.random().toString(36).substr(2, 9),
            createdAt: new Date().toISOString().substring(0, 10),
            paymentDue: document.querySelector("#invoiceDate").value,
            description: document.querySelector("#description").value,
            paymentTerms: document.querySelector("#dropdownMenu").textContent.substring(4, 6).trim(),
            clientName: document.querySelector("#clientName").value,
            clientEmail: document.querySelector("#clientEmail").value,
            status: statusType,
            senderAddress: {
                street: document.querySelector("#senderAddress").value,
                city: document.querySelector("#senderCity").value,
                postCode: document.querySelector("#senderPostcode").value,
                country: document.querySelector("#senderCountry").value,
            },
            clientAddress: {
                street: document.querySelector("#clientAddress").value,
                city: document.querySelector("#clientCity").value,
                postCode: document.querySelector("#clientPostcode").value,
                country: document.querySelector("#clientCountry").value,
            },
            items: [
                ...listOfInvoiceItems
            ],
            total: runningTotal
        }
        localStorage.setItem(createNewInvoice.id, JSON.stringify(createNewInvoice));
        setInvoices([...invoices, createNewInvoice]);
        setNumberOfInvoices(numberOfInvoices + 1);
        closeSideBar();
    }
    const background = dayOrNight == "sun" ? {

    } : {

    }
    function parentBackground(value) {
        value == "sun" ? setDayOrNight("moon") : setDayOrNight("sun");
        props.setB(value);
    }

    return (
        <div id="homePage">
            <Router>
                <Switch>
                    <Route exact path="/">
                        {/* this is home page */}

                        <div className="sidebarContainer"
                            style={dayOrNight == "sun" ? { backgroundColor: "white" } : { backgroundColor: "#252945" }}
                        >
                            <SideBar
                                sunOrMoon={value => parentBackground(value)}
                                backgroundColor={dayOrNight}
                            />
                            <NewInvoice backgroundColor={dayOrNight} />
                            <div id="saveOrDeleteBtn"
                                style={dayOrNight == "sun" ? { background: "white" } : { background: "rgb(37, 41, 69)" }}
                            >
                                <button id="discard" onClick={() => closeSideBar()}>Discard</button>
                                <button id="saveAsDraft" type="submit"
                                    onClick={() => newInvoice("draft")}
                                    style={dayOrNight == "sun" ? { color: "#888eb0" } : { color: "#DFE3FA" }}
                                >Save as draft</button>
                                <button id="saveAndSend" type="submit"
                                    onClick={() => newInvoice("pending")}>
                                    Save &amp; send</button>
                            </div>
                        </div>

                        <MainPageHeading
                            sort={value => setSortBy(value)}
                            invoiceNumber={numberOfInvoices}
                            backgroundColor={dayOrNight}
                        />
                        <div id="listOfInvoices">

                            {/* sorting by All */}
                            {invoices && sortBy == "All" &&
                                numberOfInvoices > 0 &&
                                invoices.map(item => (
                                    <Invoice
                                        key={item.id}
                                        id={item.id}
                                        paymentDue={item.paymentDue}
                                        clientName={item.clientName}
                                        total={item.total}
                                        status={item.status}
                                        sendId={(id) => setClickedId(id)}
                                        backgroundColor={dayOrNight}
                                    />
                                ))
                                ||
                                // if there is no invoice at all, display empty image
                                // (invoiceNumbers(0) && 
                                (sortBy == "All" && <NoInvoice background={dayOrNight} />)
                            }
                            {/* sorting by Pending */}
                            {invoices && sortBy == "Pending" &&
                                invoices.filter(item => item.status == "pending").length > 0 &&
                                invoices.filter(item => item.status == "pending")
                                    .map(item => (
                                        <Invoice
                                            key={item.id}
                                            id={item.id}
                                            paymentDue={item.paymentDue}
                                            clientName={item.clientName}
                                            total={item.total}
                                            status={item.status}
                                            sendId={(id) => setClickedId(id)}
                                            backgroundColor={dayOrNight}
                                        />
                                    ))

                                ||
                                // if there is no pending invoice, display empty image
                                (invoices && sortBy == "Pending" && <NoInvoice background={dayOrNight} />)

                            }
                            {/* sorting by draft */}
                            {invoices && sortBy == "Draft" &&
                                invoices.filter(item => item.status == "draft").length > 0 &&
                                invoices.filter(item => item.status == "draft")
                                    .map(item => (
                                        <Invoice
                                            key={item.id}
                                            id={item.id}
                                            paymentDue={item.paymentDue}
                                            clientName={item.clientName}
                                            total={item.total}
                                            status={item.status}
                                            sendId={(id) => setClickedId(id)}
                                            backgroundColor={dayOrNight}
                                        />
                                    ))

                                ||
                                // if there is no draft invoice, display empty image
                                (invoices && sortBy == "Draft" && <NoInvoice background={dayOrNight} />)
                            }
                            {/* sorting by paid */}
                            {invoices && sortBy == "Paid" &&
                                invoices.filter(item => item.status == "paid").length > 0 &&
                                invoices.filter(item => item.status == "paid")
                                    .map(item => (
                                        <Invoice
                                            key={item.id}
                                            id={item.id}
                                            paymentDue={item.paymentDue}
                                            clientName={item.clientName}
                                            total={item.total}
                                            status={item.status}
                                            sendId={(id) => setClickedId(id)}
                                            backgroundColor={dayOrNight}
                                        />
                                    ))

                                ||
                                // if there is no paid invoice, display empty image
                                (invoices && sortBy == "Paid" && <NoInvoice background={dayOrNight} />)
                            }
                        </div>
                    </Route>
                    <Route exact path="/view-invoice">
                        {/* view invoice page */}
                        <div className="sidebarContainer"
                            style={dayOrNight == "sun" ? { backgroundColor: "white" } : { backgroundColor: "#252945" }}
                        >
                            <SideBar
                                sunOrMoon={value => parentBackground(value)}
                                backgroundColor={dayOrNight}
                            />
                            <NewInvoice backgroundColor={dayOrNight} />
                            <div id="cancelOrSave"
                                style={dayOrNight == "sun" ? { backgroundColor: "white" } : { backgroundColor: "#141625" }}
                            >
                                <button id="cancel" onClick={() => closeSideBar()}
                                    style={dayOrNight == "sun" ? { backgroundColor: "#F9FAFE" } : { backgroundColor: "#252945" }}
                                >Cancel</button>
                                <button id="saveChanges" onClick={() => saveChanges()}>Save Changes</button>
                            </div>
                        </div>
                        <ViewInvoice
                            data={invoices}
                            id={clickedId}
                            markAsPaid={id => changeStatus(id, "paid")}
                            isPending={id => changeStatus(id, "pending")}
                            delete={id => setDeleteId(id)}
                            status={status => setStatus(status)}
                            background={dayOrNight}

                        />
                        <ConfirmDeletion
                            id={deleteId}
                            deleteInvoice={id => deleteInvoice(id)}
                            background={dayOrNight}
                        />
                    </Route>
                </Switch>
            </Router>
        </div>
    )
}