import React from "react";
import "./ViewInvoiceItems.css";

export default function ViewInvoiceItems(props) {
    return (
        <tr>
            <td className="invoiceName"
                style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
            >{props.name}</td>
            <td className="invoiceQuantity"
                style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
            >{props.quantity}</td>
            <td className="invoicePrice"
                style={props.background == "sun" ? { color: "#7E88C3" } : { color: "#DFE3FA" }}
            >${props.price}</td>
            <td className="invoiceTotal"
                style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
            >${props.total}</td>
        </tr>
    )
}