import React from "react";
import "./NoInvoice.css";

export default function NoInvoice(props) {
    return (
        <div id="noInvoice">
            <img src="./assets/illustration-empty.svg" />
            <h2
                style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
            >There is nothing here</h2>
            <div
                style={props.background == "sun" ? { color: "#888EB0" } : { color: "DFE3FA" }}
            >  Create an invoice by clicking the
                New Invoice button and get started</div>
        </div>
    )
}