import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./Invoice.css";

export default function Invoice(props) {
    const [invoiceStatus, setInoviceStatus] = useState(null);

    function setStatusInfo() {
        if (props.status === "paid") {
            setInoviceStatus({
                name: "Paid",
                color: "#33D69F",
                background: props.backgroundColor == "sun" ? "#f1f8e9" : "rgba(51, 214, 159, 0.1)"
                // background + 0.1 opacity to make it transparent
            })
        }
        else if (props.status === "pending") {
            setInoviceStatus({
                name: "Pending",
                color: "#FF8F00",
                background: props.backgroundColor == "sun" ? "#ffffea" : "rgba(255, 143, 0, 0.1)"
            })
        }
        else {
            setInoviceStatus({
                name: "Draft",
                color: props.backgroundColor == "sun" ? "#373B53" : "#DFE3FA",
                background: props.backgroundColor == "sun" ? "rgb(248, 248, 251)" : "rgba(55, 59, 83, 0.1)"
            })
        }
    }
    useEffect(() => setStatusInfo(), [props.status, props.backgroundColor]);
    const styling = {
        width: "8px",
        height: "8px",
        display: "inline - block",
        borderRadius: "50px",
        marginRight: "8px",
        background: invoiceStatus && invoiceStatus.color
    }
    return (
        <Link className="invoice" to="/view-invoice" onClick={() => props.sendId(props.id)}
            style={props.backgroundColor == "sun" ? { backgroundColor: "white" } : { backgroundColor: "#1E2139" }}>
            <div className="invoiceId">
                <span className="invoiceIdFirst">#</span>
                <span className="invoiceIdLast"
                    style={props.backgroundColor == "sun" ? { color: "#0c0e16" } : { color: "white" }}
                >{props.id}</span>
            </div>
            <div className="paymentDue"
                style={props.backgroundColor == "sun" ? { color: "#888eb0" } : { color: "#DFE3FA" }}
            >Due {props.paymentDue}</div>
            <div className="clientName"
                style={props.backgroundColor == "sun" ? { color: "#858bb2" } : { color: "white" }}
            >{props.clientName}</div>
            <h3
                style={props.backgroundColor == "sun" ? { color: "#0c0e16" } : { color: "white" }}
            >${props.total}</h3>
            <div className="invoiceStatus" style={invoiceStatus && {
                color: invoiceStatus.color,
                backgroundColor: invoiceStatus.background
            }
            }>
                <span className="circle" style={invoiceStatus && styling}></span>
                <span className="status">{invoiceStatus && invoiceStatus.name}</span>
            </div>
            <img src="./assets/icon-arrow-right.svg" className="invoice-arrow-right" />
        </Link>
    )
}