import React, { useEffect, useState } from "react";
import "./MainPageHeading.css";

export default function MainPageHeading(props) {
    const [width, setWidth] = useState(window.innerWidth);

    function checksize() {
        setWidth(window.innerWidth);
    }

    function onClick() {
        document.querySelector("#homePage").style = "background: rgba(0, 0, 0, 0.5)";
        if (props.backgroundColor == "sun") {
            document.querySelector("#formElement").style = "display:block; background:rgba(0, 0, 0, 0.06)";
            document.querySelector("#saveOrDeleteBtn").style = "display:flex; align-items:center; justify-content:space-around; background:white";
            document.querySelector(".sidebarContainer").style = "height:1024px;";
        } else {
            document.querySelector(".sidebarContainer").style = "height:1024px;background: #252945;";
            document.querySelector("#formElement").style = "display:block; background:#252945";
            document.querySelector("#saveOrDeleteBtn").style = "display:flex; align-items:center; justify-content:space-around; background:#252945";
        }
        document.querySelector("#invoiceHeading").textContent = "New Invoice";
        if (window.innerWidth > 992) {
            if (props.backgroundColor == "sun") {
                document.querySelector("#sidebar").style = "height:100%; background-color:#494e6e";
            } else {
                document.querySelector("#sidebar").style = "height:100%; background-color:#1E2139";
            }

        } else {
            if (props.backgroundColor == "sun") {
                document.querySelector("#sidebar").style = "height:100vw; background-color:#494e6e";
            } else {
                document.querySelector("#sidebar").style = "height:100vw; background-color:#1E2139";
            }
        }
    }

    window.onresize = checksize;
    window.onload = checksize;
    function checkboxIsClicked(event) {
        var checkboxes = document.getElementsByClassName("checkbox");
        if (event.target.getAttribute("class") != "checkbox") {
            return;
        }
        props.sort(event.target.nextSibling.textContent);
        for (let i = 0; i < checkboxes.length; i++) {
            if (checkboxes[i].getAttribute("id") != event.target.getAttribute("id")) {
                checkboxes[i].checked = false;
            }
        }
    }
    const styling = props.backgroundColor == "sun" ? { color: "#888EB0" } : { color: "#DFE3FA" }
    return (
        <div id="mainPageHeading">
            <div>
                <h1
                    style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                >Invoices</h1>
                {
                    props.invoiceNumber == 1 ? <h3 style={styling}>There is 1 invoice</h3>
                        : props.invoiceNumber == 0 ? <h3 style={styling}>There is no invoice</h3>
                            : <h3 style={styling}>There are {props.invoiceNumber} total invoices</h3>
                }
            </div>
            <div id="mainPageOptions">
                <div className={width < 992 ? "btn-group dropup" : "dropdown"} id="firstdropdown">
                    <button className="btn btn-secondary dropdown-toggle"
                        type="button" id="dropdownMenuButton" data-toggle="dropdown"
                        aria-haspopup="true" aria-expanded="false"
                        style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                    >
                        Filter by status
                    </button>
                    <div className="dropdown-menu dropdownContainer" aria-labelledby="dropdownMenuButton"
                        onClick={checkboxIsClicked}
                        style={props.backgroundColor == "sun" ?
                            { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
                    >
                        <div className="dropdown-item">
                            <input type="checkbox" id="first" className="checkbox" />
                            <span style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                            >All</span>
                        </div>
                        <div className="dropdown-item">
                            <input type="checkbox" id="second" className="checkbox" />
                            <span style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                            >Draft</span>
                        </div>
                        <div className="dropdown-item">
                            <input type="checkbox" id="third" className="checkbox" />
                            <span style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                            >Pending</span>
                        </div>
                        <div className="dropdown-item">
                            <input type="checkbox" id="fourth" className="checkbox" />
                            <span style={props.backgroundColor == "sun" ? { color: "#0C0E16" } : { color: "white" }}
                            >Paid</span>
                        </div>
                    </div>
                </div>
                <button id="newInvoice" onClick={() => onClick()}>
                    <span id="plusContainer">
                        <img src="./assets/icon-plus.svg" />
                    </span>
                    <span id="invoiceText">
                        New Invoice
                    </span>
                </button>
            </div>
        </div>
    )

}