import React from "react";
import "./ViewInvoiceStatus.css";
import ViewInvoiceStats from "./ViewInvoiceStats";
export default function ViewInvoiceStatus(props) {

    function edit(id) {
        document.querySelector("#homePage").style = "background: rgba(0, 0, 0, 0.5)";
        let fouundInvoice = props.invoice;
        document.querySelector("#invoiceHeading").textContent = "Edit #" + props.id;
        document.querySelector("#invoiceDate").value = fouundInvoice.paymentDue;
        document.querySelector("#description").value = fouundInvoice.description;
        document.querySelector("#dropdownMenu").textContent =
            fouundInvoice.paymentTerms == "1" ? "Net " + fouundInvoice.paymentTerms + " Day" :
                "Net " + fouundInvoice.paymentTerms + " Days"
        document.querySelector("#clientName").value = fouundInvoice.clientName;
        document.querySelector("#clientEmail").value = fouundInvoice.clientEmail;
        document.querySelector("#senderAddress").value = fouundInvoice.senderAddress.street;
        document.querySelector("#senderCity").value = fouundInvoice.senderAddress.city;
        document.querySelector("#senderPostcode").value = fouundInvoice.senderAddress.postCode;
        document.querySelector("#senderCountry").value = fouundInvoice.senderAddress.country;
        document.querySelector("#clientAddress").value = fouundInvoice.clientAddress.street;
        document.querySelector("#clientCity").value = fouundInvoice.clientAddress.city;
        document.querySelector("#clientPostcode").value = fouundInvoice.clientAddress.postCode;
        document.querySelector("#clientCountry").value = fouundInvoice.clientAddress.country;
        props.sendStatus(props.status);

        let width1 = window.innerWidth;
        if (width1 == 1027) {
            document.querySelector("#sidebar").style = "height:100vw";
        }

        let width2 = window.innerWidth;

        if (width2 < 992) {
            document.querySelector("#viewInvoice").style = "display:none";
            console.log("viewInvoicestatus.js");
            if (props.background == "sun") {
                document.querySelector("#sidebar").style = "background-color: rgb(73, 78, 110); height:100vw";
                document.querySelector("#cancelOrSave").style = "width:100%; bottom:-150px; background-color:white;";
            } else {
                document.querySelector("#sidebar").style = "background-color: rgb(30, 33, 57); height:100vw";
                document.querySelector("#cancelOrSave").style = "width:100%; bottom:-150px; background-color:#rgb(37, 41, 69);";
            }
        } else {
            if (props.background == "sun") {
                document.querySelector("#sidebar").style = "background-color: rgb(73, 78, 110); height:100%";
            } else {
                document.querySelector("#sidebar").style = "background-color: rgb(30, 33, 57); height:100%";
            }
        }
        if (props.background == "sun") {
            document.querySelector("#formElement").style = "display:block; background-color: rgba(0, 0, 0, 0.06)";
            document.querySelector(".sidebarContainer").style = "background-color:white; height:1024px";
            console.log("meeeeee");

        } else {
            document.querySelector("#formElement").style = "display:block; background-color: rgb(37, 41, 69)";
            document.querySelector(".sidebarContainer").style = "background-color:rgb(37, 41, 69); height:1024px";
            console.log("me")
        }

    }
    function deleteInvoice(id) {
        document.querySelector("#homePage").style = "height:850px; background: rgba(0, 0, 0, 0.5)";
        if (props.background == "sun") {
            document.querySelector("#sidebar").style = "filter: brightness(0.5);background-color: rgb(73, 78, 110)";
            document.querySelector("#confirmDeletion").style = "display:block; background-color:white";
            console.log("first")
        } else {
            document.querySelector("#sidebar").style = "filter: brightness(0.5);background-color: rgb(30, 33, 57)";
            document.querySelector("#confirmDeletion").style = "display:block; background-color: rgb(30, 33, 57)";
            console.log("second");
        }

        if (window.innerWidth < 992) {
            document.querySelector("#homePage").style = "background: rgba(0, 0, 0, 0.5); height:1500px";
        }

        props.confirmD(id);
    }
    return (
        <>
            <div id="viewInvoiceStatus"
                style={props.background == "sun" ? { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
            >
                {
                    props.status == "pending" ?
                        <div id="pending">
                            <div className="first-con">
                                <span className="status"
                                    style={props.background == "sun" ? { color: "#858BB2" } : { color: "#DFE3FA" }}
                                >Status</span>
                                <span className="status-icon"
                                    style={props.background == "sun" ? { backgroundColor: "#ffffea" } : { backgroundColor: "rgba(255, 143, 0, 0.1)" }}
                                >
                                    <span className="circle"></span>
                                    <span className="stats">Pending</span>
                                </span>
                            </div>
                            <div className="second-con"
                                style={props.background == "sun" ? { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
                            >
                                <button className="edit" onClick={() => edit()}
                                    style={props.background == "sun" ? { backgroundColor: "#f9fafe", color: "#7e88c3" } :
                                        { backgroundColor: "#252945", color: "#DFE3FA" }}
                                >Edit</button>

                                <button className="delete" onClick={() =>
                                    deleteInvoice(props.id)
                                }>Delete</button>

                                <button className="mark-as-paid" onClick={() => {
                                    props.markAsPaid(props.id)
                                }
                                }>Mark as Paid</button>
                            </div>

                        </div>
                        :
                        props.status == "paid" ?
                            <div id="paid">
                                <div className="first-con">
                                    <span className="status" style={props.background == "sun" ? { color: "#858BB2" } : { color: "#DFE3FA" }}
                                    >Status</span>
                                    <span className="status-icon"
                                        style={props.background == "sun" ? { backgroundColor: "#f1f8e9" } : { backgroundColor: "rgba(51, 214, 159, 0.1)" }}
                                    >
                                        <span className="circle"></span>
                                        <span className="stats">Paid</span>
                                    </span>
                                </div>
                                <div className="second-con"
                                    style={props.background == "sun" ? { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
                                >
                                    <button className="edit" onClick={() => edit()}
                                        style={props.background == "sun" ? { backgroundColor: "#f9fafe", color: "#7e88c3" } :
                                            { backgroundColor: "#252945", color: "#DFE3FA" }}
                                    >Edit</button>

                                    <button className="delete" onClick={
                                        () => deleteInvoice(props.id)
                                    }>Delete</button>

                                </div>
                            </div>
                            :
                            <div id="draft">
                                <div className="first-con">
                                    <span className="status" style={props.background == "sun" ? { color: "#858BB2" } : { color: "#DFE3FA" }}
                                    >Status</span>
                                    <span className="status-icon"
                                        style={props.background == "sun" ? { backgroundColor: "rgb(248, 248, 251)" }
                                            : { backgroundColor: "rgba(55, 59, 83, 0.1)" }}
                                    >
                                        <span className="circle"
                                            style={props.background == "sun" ? { backgroundColor: "rgb(55, 59, 83)" } : { backgroundColor: "rgb(223, 227, 250)" }}
                                        ></span>
                                        <span className="stats" style={props.background == "sun" ? { color: "#373B53" } : { color: "#DFE3FA" }}
                                        >Draft</span>
                                    </span>
                                </div>
                                <div className="second-con"
                                    style={props.background == "sun" ? { backgroundColor: "white" } : { backgroundColor: "rgb(30, 33, 57)" }}
                                >
                                    <button className="edit" onClick={() => edit(props.id)}
                                        style={props.background == "sun" ? { backgroundColor: "#f9fafe", color: "#7e88c3" } :
                                            { backgroundColor: "#252945", color: "#DFE3FA" }}
                                    >Edit</button>

                                    <button className="delete" onClick={
                                        () => deleteInvoice(props.id)
                                    }>Delete</button>

                                    <button className="mark-as-pending" onClick={
                                        () => { props.markAsPending(props.id) }
                                    }>Mark as Pending</button>
                                </div>
                            </div>
                }
            </div>
            {/* second part */}
            <ViewInvoiceStats
                data={props.invoice}
                background={props.background}
            />

        </>
    )
}