import React, { useState } from "react";
import "./ViewInvoice.css";
import { Link } from "react-router-dom";
import ViewInvoiceStatus from "./ViewInvoiceStatus";

export default function ViewInvoice(props) {
    const [status, setStatus] = useState();
    return (
        <div id="viewInvoice">
            <Link to="/" id="goback">
                <img src="./assets/icon-arrow-left.svg" />
                <span
                    style={props.background == "sun" ? { color: "#0c0e16" } : { color: "white" }}
                >Go back</span>
            </Link>

            {props.data && props.data.filter(item => item.id == props.id).map(
                item => (
                    <ViewInvoiceStatus
                        key={item.id}
                        status={item.status}
                        delete={id => props.deleteInvoice(id)}
                        id={item.id}
                        invoice={item}
                        markAsPaid={id => props.markAsPaid(id)}
                        markAsPending={id => props.isPending(id)}
                        confirmD={id => props.delete(id)}
                        sendStatus={status => props.status(status)}
                        background={props.background}
                    />

                )
            )}

        </div>
    )
}