import React, { useEffect } from "react";
import "./ConfirmDeletion.css";
import { Link } from "react-router-dom";

export default function ConfirmDeletion(props) {
    function resetStyling(id) {
        props.deleteInvoice(id);
        document.querySelector("#homePage").style = "background: #F2F2F2;";
        document.querySelector("#viewInvoice").style = "filter: brightness(1)";
        if (props.background == "sun") {
            document.querySelector("#sidebar").style = "filter: brightness(1);background-color: rgb(73, 78, 110)";
        } else {
            document.querySelector("#sidebar").style = "filter: brightness(1);background-color: rgb(30, 33, 57)";
        }
        document.querySelector("#confirmDeletion").style = "display:none;";

    }
    function goBack() {
        document.querySelector("#homePage").style = "background: #F2F2F2;";
        document.querySelector("#viewInvoice").style = "filter: brightness(1)";
        if (props.background == "sun") {
            document.querySelector("#sidebar").style = "filter: brightness(1);background-color: rgb(73, 78, 110)";
        } else {
            document.querySelector("#sidebar").style = "filter: brightness(1);background-color: rgb(30, 33, 57)";
        }
        document.querySelector("#confirmDeletion").style = "display:none;";

    }
    return (
        <div id="confirmDeletion"
            style={props.background == "sun" ? { backgroundColor: "white" }
                : { backgroundColor: "#1E2139" }}
        >
            <div id="heading"
                style={props.background == "sun" ? { color: "#0C0E16" } : { color: "white" }}
            >Confirm Deletion</div>
            <div id="text"
                style={props.background == "sun" ? { color: "#888EB0" } : { color: "#DFE3FA" }}
            >Are you sure you want to delete invoice {props.id}? This action cannot be undone.</div>
            <div id="con">
                <button id="cancel" onClick={() => goBack()}
                    style={props.background == "sun" ? { color: "#7E88C3", backgroundColor: "rgb(249, 250, 254)" }
                        : { color: "#DFE3FA", backgroundColor: "rgb(37, 41, 69)" }}
                >Cancel</button>
                <Link to="/">
                    <button id="delete" onClick={() => resetStyling(props.id)}>Delete</button>
                </Link>
            </div>
        </div>
    )
}